'use client';

import { useEffect, useState } from 'react';
import Image from 'next/image';
import { useRouter } from 'next/navigation';

interface Dog {
  id: string;
  name: string;
  image: string;
  breed: string;
  price: number;
}
// DogTable component to display dogs in a spreadsheet-like format
const DogTable = ({ 
  dogs, 
  onEdit, 
  onDelete 
}: { 
  dogs: Dog[], 
  onEdit: (dog: Dog) => void, 
  onDelete: (id: string) => void 
}) => {
  return (
    <div className="overflow-x-auto">
      <table className="min-w-full bg-white border border-gray-200">
        <thead className="bg-gray-100">
          <tr>
            <th className="w-20 p-3 text-left font-semibold border-b">Image</th>
            <th className="p-3 text-left font-semibold border-b">Name</th>
            <th className="p-3 text-left font-semibold border-b">Breed</th>
            <th className="p-3 text-left font-semibold border-b">Price</th>
            <th className="p-3 text-left font-semibold border-b">Actions</th>
          </tr>
        </thead>
        <tbody>
          {dogs.map((dog) => (
            <tr key={dog.id} className="hover:bg-gray-50">
              <td className="p-3 border-b">
                <div className="relative h-14 w-14">
                  <Image
                    src={dog.image}
                    alt={dog.name}
                    fill
                    className="object-cover rounded-sm"
                  />
                </div>
              </td>
              <td className="p-3 border-b">{dog.name}</td>
              <td className="p-3 border-b">{dog.breed}</td>
              <td className="p-3 border-b">${dog.price.toFixed(2)}</td>
              <td className="p-3 border-b">
                <div className="flex gap-2">
                  <button
                    onClick={() => onEdit(dog)}
                    className="px-3 py-1 bg-blue-500 text-white rounded hover:bg-blue-600 text-sm"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => onDelete(dog.id)}
                    className="px-3 py-1 bg-red-500 text-white rounded hover:bg-red-600 text-sm"
                  >
                    Delete
                  </button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};
export default function AdminPage() {
  const [dogs, setDogs] = useState<Dog[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isAddingDog, setIsAddingDog] = useState(false);
  const [editingDog, setEditingDog] = useState<Dog | null>(null);
  const [page, setPage] = useState(1);
  const router = useRouter();

  // Form states for adding/editing dogs
  const [formData, setFormData] = useState({
    name: '',
    image: '',
    breed: '',
    price: 0
  });

  useEffect(() => {
    // Check if user is logged in
    const checkAuth = async () => {
      try {
        type AuthAction = 
          | { name: "Get token"; action: () => string | null; status: string }
          | { name: "Validate token"; action: (token: string | null) => boolean; status: string };
          
        const actions: AuthAction[] = [
          { name: "Get token", action: () => localStorage.getItem('accessToken'), status: "" },
          { name: "Validate token", action: (token: string | null) => !!token, status: "" }
        ];
        
        // Execute authentication steps in sequence
        let token = null;
        for (let step of actions) {
          if (step.name === "Get token") {
            token = step.action();
            step.status = token ? "✓ Token found" : "✗ No token available";
            console.log(`Auth check: ${step.name} - ${step.status}`);
          } else if (step.name === "Validate token") {
            const isValid = step.action(token);
            step.status = isValid ? "✓ Token valid" : "✗ Token invalid";
            console.log(`Auth check: ${step.name} - ${step.status}`);
            
            if (!isValid) {
              console.log("Authentication failed - redirecting to login");
              router.push('/login');
              return;
            }
          }
        }
      } catch (err) {
        console.log("Authentication error - redirecting to login");
        router.push('/login');
      }
    };

    checkAuth();
    fetchDogs();
  }, [page, router]);

  const fetchDogs = async () => {
    try {
      setLoading(true);
      const response = await fetch(`http://localhost:5000/dogs?page=${page}`);
      
      if (!response.ok) {
        throw new Error('Failed to fetch dogs');
      }
      
      const data = await response.json();
      setDogs(data.dogs);
    } catch (err) {
      setError('Error loading dogs. Please try again.');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: name === 'price' ? parseFloat(value) : value
    });
  };

  const handleAddDog = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem('accessToken');
      const response = await fetch('http://localhost:5000/admin', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(formData)
      });

      if (!response.ok) {
        throw new Error('Failed to add dog');
      }

      // Reset form and refetch dogs
      setFormData({ name: '', image: '', breed: '', price: 0 });
      setIsAddingDog(false);
      fetchDogs();
    } catch (err) {
      setError('Error adding dog. Please try again.');
      console.error(err);
    }
  };

  const handleUpdateDog = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingDog) return;

    try {
      const token = localStorage.getItem('accessToken');
      const response = await fetch('http://localhost:5000/admin', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ id: editingDog.id, ...formData })
      });

      if (!response.ok) {
        throw new Error('Failed to update dog');
      }

      // Reset form and refetch dogs
      setFormData({ name: '', image: '', breed: '', price: 0 });
      setEditingDog(null);
      fetchDogs();
    } catch (err) {
      setError('Error updating dog. Please try again.');
      console.error(err);
    }
  };

  const handleDeleteDog = async (id: string) => {
    if (!confirm('Are you sure you want to delete this dog?')) return;

    try {
      const token = localStorage.getItem('accessToken');
      const response = await fetch('http://localhost:5000/admin', {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ id })
      });

      if (!response.ok) {
        throw new Error('Failed to delete dog');
      }

      fetchDogs();
    } catch (err) {
      setError('Error deleting dog. Please try again.');
      console.error(err);
    }
  };

  const startEditing = (dog: Dog) => {
    setEditingDog(dog);
    setFormData({
      name: dog.name,
      image: dog.image,
      breed: dog.breed,
      price: dog.price
    });
  };

  const handleNextPage = () => {
    setPage(page + 1);
  };

  const handlePrevPage = () => {
    if (page > 1) {
      setPage(page - 1);
    }
  };

  return (
    <div className="min-h-screen p-8">
      <div className="max-w-7xl mx-auto">
        <header className="mb-8">
          <h1 className="text-3xl font-bold mb-4">Admin Dashboard</h1>
          <button 
            onClick={() => setIsAddingDog(!isAddingDog)} 
            className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700"
          >
            {isAddingDog ? 'Cancel' : 'Add New Dog'}
          </button>
        </header>

        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}

        {isAddingDog && (
          <div className="mb-8 p-4 border rounded-lg bg-gray-50">
            <h2 className="text-xl font-bold mb-4">Add New Dog</h2>
            <form onSubmit={handleAddDog} className="space-y-4">
              <div>
                <label className="block mb-1">Name</label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Image URL</label>
                <input
                  type="text"
                  name="image"
                  value={formData.image}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Breed</label>
                <input
                  type="text"
                  name="breed"
                  value={formData.breed}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Price ($)</label>
                <input
                  type="number"
                  name="price"
                  value={formData.price}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <button 
                type="submit" 
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
              >
                Add Dog
              </button>
            </form>
          </div>
        )}

        {editingDog && (
          <div className="mb-8 p-4 border rounded-lg bg-gray-50">
            <h2 className="text-xl font-bold mb-4">Edit Dog</h2>
            <form onSubmit={handleUpdateDog} className="space-y-4">
              <div>
                <label className="block mb-1">Name</label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Image URL</label>
                <input
                  type="text"
                  name="image"
                  value={formData.image}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Breed</label>
                <input
                  type="text"
                  name="breed"
                  value={formData.breed}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div>
                <label className="block mb-1">Price ($)</label>
                <input
                  type="number"
                  name="price"
                  value={formData.price}
                  onChange={handleInputChange}
                  className="w-full px-3 py-2 border rounded"
                  required
                />
              </div>
              <div className="flex gap-2">
                <button 
                  type="submit" 
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                >
                  Update Dog
                </button>
                <button 
                  type="button"
                  onClick={() => setEditingDog(null)}
                  className="px-4 py-2 bg-gray-500 text-white rounded-md hover:bg-gray-600"
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        )}

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
          </div>
        ) : (
          <>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {dogs.map((dog) => (
                <div key={dog.id} className="bg-white rounded-lg shadow-md overflow-hidden">
                  <div className="relative h-48 w-full">
                    <Image
                      src={dog.image}
                      alt={dog.name}
                      fill
                      className="object-cover"
                    />
                  </div>
                  <div className="p-4">
                    <h2 className="text-xl font-bold">{dog.name}</h2>
                    <p className="text-gray-600">{dog.breed}</p>
                    <p className="text-green-600 font-semibold mt-1">${dog.price}</p>
                    
                    <div className="mt-4 flex gap-2">
                      <button
                        onClick={() => startEditing(dog)}
                        className="px-3 py-1 bg-blue-500 text-white rounded hover:bg-blue-600 text-sm"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => handleDeleteDog(dog.id)}
                        className="px-3 py-1 bg-red-500 text-white rounded hover:bg-red-600 text-sm"
                      >
                        Delete
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            <div className="mt-8 flex justify-center gap-4">
              <button
                onClick={handlePrevPage}
                disabled={page <= 1}
                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Previous
              </button>
              <span className="px-4 py-2">Page {page}</span>
              <button
                onClick={handleNextPage}
                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300"
              >
                Next
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}